body {
  margin: 0;
  background-image: url("./background.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

html {
  font-family: "Poppins", sans-serif;
  color: #1d355d;
}

#root {
  padding-inline-start: 20px;
  padding-inline-end: 20px;
}

button {
  cursor: pointer;
}

.main-content {
  position: relative;
  margin-block-end: 2rem;
}

.adventureImage {
  position: absolute;
  right: 0;
  top: 4px;
  max-width: 50%;
}

img {
  max-width: 100px;
  filter: drop-shadow(0px 4px 24px rgba(0, 0, 0, 0.1));
}

.wrapper {
  background-color: white;
  padding: 32px;
  margin-block-end: 32px;
  padding-block-start: 64px;
  padding-block-end: 9px;
  margin-inline-end: 34px;
  border-radius: 24px;
}

h1 {
  font-size: 36px;
  line-height: 54px;
  padding-inline-start: 20px;
  text-transform: uppercase;
  color: white;
}

h2 {
  font-size: 48px;
  line-height: 72px;
  padding-block-start: 30px;
}

h3 {
  font-size: 24px;
  line-height: 36px;
  color: #2f527b;
}

h4 {
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  padding-block-end: 60px;
  margin-block-start: -20px;
}

.flag {
  width: 84px;
  height: 54px;
  margin-block-end: -16px;
}

.questions {
  font-size: 24px;
  line-height: 36px;
  padding-inline-end: 16px;
  padding-block-start: 7px;
}

button {
  font-size: 18px;
  line-height: 27px;
  padding-block-start: 12px;
  padding-block-end: 13px;
  padding-inline-start: 19px;
  padding-inline-end: 40px;
  width: 93%;
  text-align: start;
  border-radius: 12px;
  margin-block-end: 25px;
  background-color: white;
  color: rgba(96, 102, 208, 0.8);
  border: 2px solid rgba(96, 102, 208, 0.7);
}

button:hover:not(.rightAnswer, .wrongAnswer, .nextBtn, .resultBtn) {
  background-color: #f9a826;
  color: white;
  border: 2px solid #ffffff;
  transition: 0.2s;
}
button[disabled]:hover {
  cursor: not-allowed;
}

button[disabled]:hover:not(.rightAnswer, .wrongAnswer) {
  background-color: white;
  color: rgba(96, 102, 208, 0.8);
  border: 2px solid rgba(96, 102, 208, 0.7);
}

.wrapper form {
  padding-block-start: 7px;
}

.rightAnswer {
  background-color: #60bf88;
  color: #ffff;
  border: 2px solid #ffffff;
}

.checkRight {
  background-image: url("./assets/check_circle_outline-24px.svg");
  background-repeat: no-repeat;
  background-position: 95%;
}

.wrongAnswer {
  background-color: #ea8282;
  color: #fff;
  border: 2px solid #ffffff;
}

.checkWrong {
  background-image: url("./assets/highlight_off-24px.svg");
  background-repeat: no-repeat;
  background-position: 96%;
}

.wrongAnswer:hover {
  background-color: #ea8282;
  color: #ffffff;
}

svg {
  fill: white;
}

.hiddenBtn {
  display: none;
}

span {
  font-size: 18px;
  line-height: 27px;
  padding-inline-end: 46px;
}

.score {
  color: #60bf88;
  font-size: 35px;
  font-weight: bold;
  padding-inline-end: 0;
}

.btnWrapper {
  position: relative;
}

.nextBtn-wrapper {
  text-align: end;
}

.nextBtn {
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  padding-block-start: 13px;
  padding-block-end: 12px;
  padding-inline-start: 37px;
  padding-inline-end: 37px;
  margin-block-start: -2px;
  margin-inline-end: 36px;
  width: auto;
  background-color: #f9a826;
  box-shadow: 0px 2px 4px rgba(252, 168, 47, 0.4);
  border-radius: 12px;
  border: 2px solid #f9a826;
}

.nextBtn:hover {
  border: 2px solid #f9a826;
}

.result {
  background-image: url("./undraw_winners_ao2o.svg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-position-y: 10%;
  max-width: 400px;
  margin: auto;
  background-color: white;
  padding: 2rem;
  padding-block-start: 10rem;
  margin-block-end: 2rem;
  border-radius: 24px;
  text-align: center;
}

.resultBtn {
  font-size: 18px;
  font-weight: 600;
  line-height: 27px;
  padding-block-start: 18px;
  padding-block-end: 17px;
  width: auto;
  text-align: start;
  border-radius: 12px;
  margin-block-end: -1px;
  background-color: white;
  border: 2px solid #1d355d;
  padding-inline-end: 64px;
  padding-inline-start: 63px;
  color: #1d355d;
}

.resultBtn:hover {
  background-color: white;
  border: 2px solid #1d355d;
  color: #1d355d;
}

a {
  text-decoration: none;
}

.name:nth-of-type(1)::before {
  content: " A ";
  padding-inline-end: 39px;
  font-size: 24px;
}

.name:nth-of-type(2)::before {
  content: " B ";
  padding-inline-end: 39px;
  font-size: 24px;
}

.name:nth-of-type(3)::before {
  content: " C ";
  padding-inline-end: 39px;
  font-size: 24px;
}

.name:nth-of-type(4)::before {
  content: " D ";
  padding-inline-end: 39px;
  font-size: 24px;
}

@media (max-width: 500px) {
  .wrapper {
    max-width: 464px;
    margin: auto;
  }

  .adventureImage {
    top: 8%;
  }

  h1 {
    max-width: 100px;
  }
}

@media (min-width: 500px) {
  #root {
    max-width: 488px;
    margin: auto;
    padding-inline-start: 0;
    padding-inline-end: 0;
  }

  .wrapper {
    max-width: 464px;
    margin: auto;
    padding-inline-end: 0;
  }
}
